.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body{
  margin: 0px;
  height: 100%;
  width: 100%;
  font-family: 'Inter';
}
.main-page{
  width: 100%;
}

/*=modal=*/
.modal {
  display: none; /* Hidden by default */
  position:fixed; /* Stay in place */
  z-index: 501; /* Sit on top *//* Location of the box */
  left: 0;
  top: 0px;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  background-color: rgba(0,0,0,0.4);
}

/* Modal Content */
.modal-content {
  font-family: 'Inter';
  
  box-shadow: 0 2px 114px 0 rgba(0,0,0,.3);
  text-align: center;
  font-style: normal;
  font-weight: 400;
  display: flex;
  color: #0E0E0D;
  background-color: white;
  border-radius: 10px;
  flex-direction: column;
  left: 50%;
  padding: 44px 44px 22px;
  position: absolute;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  width: 480px;
}


.modal-window-login{
  font-size: 24px;
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 20px;
  margin-bottom: 20px;

}

/*=modal=*/
.button{
  border: none;
  background-color: white;
  color: black;
  flex-shrink: 0;
  cursor: pointer;
  opacity: 1;
  font-size: 20px;
  font-weight: bold;
  border-radius: 50px;
}
.big-button{
  width: 280px;
  height: 90px;
}
.small-button{
  width: 170px;
  height: 90px;
}
.fa{
  cursor: pointer;
  opacity: 1;
}
.fa:hover{
  opacity: 0.7;
  transition: opacity 0.2s ease-in-out;
}
.button:hover {
  opacity: 0.7;
  transition: opacity 0.2s ease-in-out;
}
.fonts{
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
header{
  top: 0;
  position: fixed;
  width: 100%;
  box-sizing: border-box;
  height: 14vh;
  display: flex;
  padding: 30px 70px;
  background-color: transparent;
  transition: background-color 0.3s ease-in-out;
  z-index: 500;
  align-items: center;
}
.logo{
  position: absolute;
  left: 50%;
  transform: translate(-50%);
 
}
.logo img {
  width: 100%;
}

header.scrolled {
  background-color: #AA3891;
}
.header-links{
  text-decoration: none;
  color: white;
  font-family: Inter;
  font-size: 20px;
  
  line-height: normal;
  cursor: pointer;
}
.navbar{
  width: 100%;
  display: flex;
  justify-content: space-between;

}
.navbar-links {
  display: flex;
  flex-direction: row;
  gap: 50px;
  align-items: center;
}
.main-page-lipglosses{
  width: 100%;
}
.main-page-lipglosses img{
  width: 100%;
}
.main-page-text {
  margin: 180px 6vw;
  text-align: center;
  font-family: Forum;
  font-size: 48px;
  font-style: normal;
  font-weight: 400;
}
footer{
  padding: 70px 6vw 30px 6vw ;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  box-sizing: border-box;
}
.footer-icons{
  width: 195px;
  display: flex;
  justify-content: space-evenly;
  cursor: pointer;
}
.footer-left{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.footer-about{
  text-align: center;
  font-family: Inter;
  font-size: 24px;
  display: flex;
  flex-direction: row;
  column-gap: 50px;
}
.about-column{
  display: flex;
  flex-direction: column;
}
.footer-right-form{
  width: 20%;
  font-family: Inter;
  font-size: 24px;
  display: flex;
  flex-direction: column;
}
.input-email{
  font-family: Inter;
  font-size: 24px;
  text-transform: capitalize;
  padding: 24px;
}
.footer-about a{
  color: black;
  text-align: center;
  font-family: Inter;
  font-size: 22px;
}
.footer-subscribe-button{
  background-color: #AA3891;
  color: #ffffff;

}
.toggle-button{
  right: 6vw;
}
.icons-top-smallscreen{
  position: absolute;
  
  display: flex;
  display: none;
  justify-content: center;
  flex-direction: row;
}
.cart-smallscreen{
  left: 6vw;

}